<template>
    <div class="main">
        <div class="dataDictionary">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn(1,0)">新增位置</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" v-if='tableData.length>0' border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%" row-key="id" lazy :tree-props="{hasChildren: 'hasChildren'}">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :width="item.widthS"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="220">
                        <template slot-scope="scope">
                            <el-button @click="addBtn(2,scope.row)" v-if="scope.row.pid==0" type="text" size="small" style="color:#67c23a">新增下级</el-button>
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!-- <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div> -->
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" class="dialogStyle" v-dialogDrag :visible.sync="dialogVisible" width="600px" v-if="dialogVisible" :close-on-click-modal="false">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="title" label="位置名称">
                                        <el-input v-model="formData.title" placeholder="请输入位置名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item prop="sort" label="排序">
                                        <el-input v-model="formData.sort" placeholder="请输入排序" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput dialogInput1">
                            <div class="padd">
                                <div>
                                    <el-form-item label="关联班级">
                                        <el-select placeholder="请选择关联班级" clearable filterable v-model="formData.classList_id">
                                            <el-option v-for="item in classList" :label="item.class_name" :value="item.id" :key="item.id"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput dialogInput1">
                            <div class="padd">
                                <div>
                                    <el-form-item label="状态" v-if="title=='编辑'">
                                        <el-switch v-model="formData.disabled"></el-switch>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div style="text-align: center;margin-top:35px">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>
                    </el-form>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    hashApi,
    locationManagementList,
    locationManagementView,
    locationManagementInfo,
    locationManagementAdd,
    locationManagementEdit,
    locationManagementDel,
} from '@/api/index';

export default {
    data() {
        return {
            // total: 0,
            // pageNum: 1,
            // pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                title: '',
                sort: "",
                classList_id: '',
                disabled: true,
            },
            method_auth: {},
            editId: '',
            title: '',
            dialogVisible: false,
            rule: {
                title: [
                    { required: true, message: '请输入位置名称', trigger: 'blur' },
                ],
                sort: [
                    { required: true, message: '请输入排序', trigger: 'blur' },
                ],
            },
            tableHead: [
                { title: 'ID', prop: 'id', widthS: "130" },
                { title: '位置名称', prop: 'title' },
                { title: '排序', prop: 'sort' },
                // { title: '字典分类', prop: 'dictionary_type' },
                // { title: '字典说明', prop: 'remarks' },

            ],
            tableData: [],
            hashData: '',
            pid: "",
            codeDisabled: false,
            level: 0,//数据层级
            classList: [],
        };
    },
    mounted() {
        this.getListData();
        this.resView();
        this.getHashData();
    },
    methods: {
        // 监听 pagesize 改变
        // handleSizeChange(newSize) {
        //     this.pageNum = 1;
        //     this.pageSize = newSize;
        //     this.getListData();
        // },
        // 监听 pagenum 改变
        // handleCurrentChange(newPage) {
        //     this.pageNum = newPage;
        //     this.getListData();
        // },
        //获取Hash
        async getHashData() {
            const res = await this.axios.post(hashApi, { page_hash: 'locationManagement' });
            if (res.data.code == 1) {
                this.hashData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        async resView() {
            const res = await this.axios.post(locationManagementView);
            if (res.data.code == 1) {
                this.classList = res.data.data.class_data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let grade_id = '';
                    let class_id = '';
                    this.classList.forEach(e => {
                        if (e.id == this.formData.classList_id) {
                            grade_id = e.grade_id;
                            class_id = e.class_id;
                        }
                    })
                    if (this.formData.disabled) {
                        this.formData.disabled = 0
                    } else {
                        this.formData.disabled = 1
                    }
                    let datas = {
                        title: this.formData.title,
                        sort: this.formData.sort,
                        grade_id: grade_id,
                        class_id: class_id,
                        hash: this.hashData,
                    };
                    if (this.title.indexOf("新增") != -1) {
                        var urls = locationManagementAdd;
                        datas.pid = this.pid;
                    } else if (this.title == '编辑') {
                        var urls = locationManagementEdit;
                        datas.id = this.editId;
                        datas.disabled = this.formData.disabled;
                    }
                    const res = await this.axios.post(urls, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error',
                        });
                    }
                    this.getHashData();
                }
            });
        },
        //重置对话框表单
        resetForm() {
            this.formData = {
                title: '',
                sort: '',
                classList_id: "",
                disabled: true,
            };
        },
        // 点击新增
        addBtn(e, s) {
            this.dialogVisible = true;
            this.codeDisabled = false;
            if (e == 1) {
                this.pid = s;
                this.title = '新增父级字典';
                this.level = 1;
            } else if (e == 2) {
                this.pid = s.id;
                this.title = '新增' + s.title + '下级字典';
                this.level = s.level + 1
            }
            this.getHashData();
            this.resetForm(); //重置表单
        },
        // 点击编辑
        editBtn(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.codeDisabled = true;
            this.editId = row.id;
            this.getInfo(row.id);
            this.getHashData();
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
        },
        //字典详情
        async getInfo(id) {
            const res = await this.axios.post(locationManagementInfo, {
                id: id,
            });
            if (res.data.code == 1) {
                this.formData = res.data.data;
                this.classList.forEach(e => {
                    if ((e.grade_id == res.data.data.grade_id) && (e.class_id == res.data.data.class_id)) {
                        console.log(e);
                        this.formData.classList_id = e.id;
                    }
                })
                console.log(this.formData.classList_id);
                if (res.data.data.disabled == 0) {
                    this.formData.disabled = true;
                } else if (res.data.data.disabled == 1) {
                    this.formData.disabled = false;
                }
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                })
            }
        },
        //删除
        delBtn(id) {
            this.$confirm('是否删除该条数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            })
                .then(async () => {
                    const datas = {
                        id: id,
                        hash: this.hashData,
                    };
                    const res = await this.axios.post(locationManagementDel, datas);
                    if (res.data.code == 1) {
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                        this.$message({ showClose: true, message: '删除成功', type: 'success', });
                    } else {
                        this.$message({ showClose: true, message: res.data.msg, type: 'error', });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
            this.getHashData();
        },
        //获取列表
        async getListData() {
            this.isLoading = true;
            let datas = {
                // curr: this.pageNum,
                // pagesize: this.pageSize,
            };
            const { data } = await this.axios.post(locationManagementList, datas);
            if (data.code == 1) {
                this.tableData = data.data;
                this.total = data.total;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        }
    }
};
</script>
<style >
.dataDictionary .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.dataDictionary {
    /deep/ .dialogStyle {
        .el-form-item {
            margin-bottom: 20px !important;
        }

        .el-form-item__label {
            line-height: 20px;
        }

        .dialogInput1 {
            .el-form-item {
                margin-bottom: 0px !important;
            }
            .el-form-item__label {
                line-height: 40px;
            }
        }
        .el-form--label-top .el-form-item__label {
            padding: 0;
        }
        .el-dialog__body {
            padding: 30px 15px 30px 25px;
        }
        .el-input-number .el-input__inner {
            text-align: left;
        }
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 10px;
        .padd {
            flex: 1;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
        .el-select {
            width: 100%;
        }
    }
    .btn {
        width: 250px;
    }
}
</style>



